<template>
    <div class="userSearch-wrap">
        <mu-flex class="userSearch-flex1" justify-content="center" align-items="center">
            <mu-flex justify-content="center" align-items="center" class="userSearch-flex2">
                <mu-container class="userSearch-container">
                    <mu-form ref="form" :model="formInfo" class="userInfo-form" label-position="top">
                        <mu-form-item prop="platform" label="请选择游戏平台">
                            <mu-select v-model="formInfo.userPlatform" color="orange500" underline-color="orange500">
                                <mu-option v-for="option in options" :key="option" :label="option" :value="option"></mu-option>
                            </mu-select>
                        </mu-form-item>
                        <mu-form-item prop="userName" label="请输入玩家昵称" :rules="usernameRules">
                            <mu-text-field v-model="formInfo.userName" color="orange500" prop="userName" underline-color="orange500"></mu-text-field>
                        </mu-form-item>
                        <mu-button color="orange500" @click="submit">搜索</mu-button>
                    </mu-form>
                </mu-container>
            </mu-flex>
        </mu-flex>
    </div>
</template>

<script>
export default {
  name: 'UserSearchView',
  data () {
    return {
      usernameRules: [
        // eslint-disable-next-line standard/object-curly-even-spacing
        { validate: (val) => !!val, message: '必须填写用户名'}
      ],
      options: ['Origin', 'PS4', 'XBox'],
      formInfo: {
        userPlatform: 'Origin',
        userName: ''
      }
    }
  },
  methods: {
    submit () {
      this.$refs.form.validate().then((result) => {
        console.log('form valid: ', result)
        this.$router.push({name: 'PlayerStatusInfoView'})
      })
    }
  }
}
</script>
<style scoped>
  .userSearch-flex1 {
    margin-top: 50%;
    width: 100%;
    height: 100%;
  };
  .mu-form-item-label {
    color: white !important;
  }
  .userSearch-flex2 {
    width: 50%;
    height: 100%;
  }
  .userSearch-container {
    width: 100%;
    height: 100%;
  }
  .userInfo-form {
    width: 100%;
    height: 100%;
    background-color: #303030;
    padding: 5px 10px 5px 10px;
  }
</style>



